.product-img {
    width: 100%;
    height: 100%;
    display: block;
    object-fit: cover;
    scale: 1;
    transition: all 0.3s ease-in-out;
  }
.product-img:hover{
    scale: 1.1;
  }
.grid{
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(260px, 1fr ));
  gap: 1rem;
}
.gallery li{
  margin: 25px 0;
}
figure {
  margin: 0;
  height: 350px;
  position: relative;
  overflow: hidden;
}
figcaption {
  transition: all 0.3s ease-in;
  transform: translate3d(0, 12rem, 0);
  position: absolute;
  inset: 0;
  pointer-events: none;
  padding: 4rem 2.5rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  gap: 2rem;
  color: white;
}
figcaption main {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  gap: 2rem;
}
figure p {
  margin: 0;
}
figure footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
figure footer img {
  width: auto;
}
figure main,
figure footer {
  opacity: 0;
}
figure:hover figcaption {
  transform: translate3d(0, 0, 0);
  background: linear-gradient(180deg, rgba(21, 22, 24, 0.24) 0%, #151618 100%);
}
figure:hover main,
figure:hover footer {
  opacity: 1;
}